import { DraggableDataProps } from '../types';
import { UPDATE_DRAGHANDER, UPDATE_DROPHOLDER, CLEAR_DROPHOLDER } from '../actions';
import updateDragHander from './updateDragHander';
import updateDropHolder from './updateDropHolder';
import clearDropHolder from './clearDropHolder';

export default function draggableReducer(draggableData: DraggableDataProps, action): DraggableDataProps {
  const { type, payload } = action;
  switch (type) {
    case UPDATE_DRAGHANDER:
      return updateDragHander(draggableData, payload);
    case UPDATE_DROPHOLDER:
      return updateDropHolder(draggableData, payload);
    case CLEAR_DROPHOLDER:
      return clearDropHolder(draggableData);
    default:
      return draggableData;
  }
}
